<template>
  <div id="app">
    
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'app',
  data(){
    return {
      type:""
    }
  },
  mounted(){
    this.type = localStorage.type;
    if(this.type == "1"){
       this.$router.push({path:'/homePage'})
    }else{
      this.$router.push({path:'/'})
    }
  }
}
</script>

<style>
*{
  padding:0;
  margin:0;
}
#app {
  width: 100%;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top:-5px;
}
</style>



