<template>
  <div class="myCenter">
    <div class="title">
        <img src="../assets/hg4_03.png" alt="" @click="returns"><h1>个人中心</h1>
    </div>
    <div class="middle">
          <img src="../assets/hg2_05.png">
          <h2>{{oldManName}}</h2>
    </div>
    <ul>
      <li @click='changepassword'>修改密码  <img src="../assets/ys2_03.png" alt=""></li>
      <li @click='scheduling'>我的排班  <img src="../assets/ys2_03.png" alt=""></li>
      <li @click='btn'>退出登录  <img src="../assets/ys2_03.png" alt=""></li>
    </ul>
    

    <div class="mask" id='mask' v-show="willShow"></div>
    <div class="setting" id='setting' v-show="willShow" >
      <h1>您确定退出登录吗？</h1>
      <span class='cancel' @click='sure' style="color: #fff;float:left;margin-left:10%;">确定</span><span class = 'sure' @click='cancel' style='background:#cccccc;float:right;margin-right:10%'>返回</span>
    </div>

  </div>
</template>

<script>
export default {
  name: 'homePage',
  data () {
    return {
      oldManName:"",
      willShow:false
    }
  },
  mounted () {   
    this.oldManName = localStorage.name

  },
  methods:{
      returns() {
        this.$router.go(-1)
      },
      btn:function(){
        if(this.willShow==false){             
          this.willShow=true;           
        }else{             
          this.willShow=false           
        } 
      },
      cancel:function(){
        this.willShow=false
      },
      sure:function(){
        var that = this;
        localStorage.setItem("type",2)
        localStorage.clear();
        that.willShow=false;
        that.$router.push({path:'/'}) 

      },
      changepassword() {
        this.$router.push({path:'/changePassword'})
      },
      scheduling() {
        this.$router.push({path:'/scheduling'})
      }

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #eee;
}
.title img{
  position: absolute;
  left: 15px;
  width: 12px;
  height: 21px;
  top: 16px;
}
.title h1{
  font-size:13px;
  color: #ff9000;
  text-align: center;
  line-height: 70px;
  font-size: 20px;
  font-weight: normal;
}
.middle{
  height: 130px;
  text-align: center;
}
.middle img{
  width: 90px;
  height: 90px;
  margin-top: 30px;
}
.middle h2{
  font-size:13px;
  margin-top: 10px;
  font-weight: normal;
}
ul{
  margin-top: 50px;
  width: 100%;
  background-color:#ffefdb;
}
ul li{
  height: 48px;
  line-height: 48px;
  font-size: 14px;
  text-align: left;
  padding-left: 36px;
  border-bottom: 1px solid #ffc77f;
  z-index: 1
}
ul li img{
  float: right;
  width: 10px;
  height: 15px;
  display: block;
  z-index: 10;
  margin-right: 36px;
  margin-top: 13px;
}
.mask{
  min-width: 320px;
  min-height: 100%;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: .5;
  z-index: 999;
  position: absolute;
  top: 0;
  left: 0;
}
.setting{
  width: 66%;
  height: 150px;
  border: 1px solid #eaeaea;
  border-radius: 15px;
  margin-left: 17%;
  background: #FFFFFF;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  z-index: 999;
}
.setting h1{
  margin-top: 15px;
  font-size: 15px;
  font-weight: normal;
  text-align: center;
  margin-bottom: 15px;
  line-height: 70px;
  color: #414141;
}
.setting span{
  font-size: 14px;
  display: block;
  width: 30%;
  float: left;
  height: 32px;
  margin-top: 5px;
  line-height: 32px;
  cursor: pointer;
  background:#ff9000;
  border-radius: 10px;
  color: #fff;
}
.setting .cancel{
  border-right: 1px solid #eaeaea;  
  color: #999999;     
}
</style>



