<template>
  <div class="hello">
   <div class="title">
     <h2>中云爱老</h2>
     <img src="../../src/assets/hg3_03.png" alt="" @click="my">
   </div>
   <div class="banner">
     <img src="../assets/bn1_02.png" alt="">
   </div>
   <h1>您好！护工{{name}}，您今日护理的老人。</h1>
   <div class="oldPeople">
     <ul>
       <li v-for="(lists,index) in list" @click="details(lists.id,lists.beadhouseId,lists.oldManName,lists.stayType,lists.photo,lists.nursingStatus)">
         <img :src="imgUrls+lists.photo" alt="" v-if="lists.photo">
         <img src='../assets/hg2_05.png' alt="" v-else>
           <p>{{lists.oldManName}}</p>
           <p>{{lists.bed}}</p>
           <input type="button" v-if="lists.nursingStatus == 0" value='待完成' class='unfinished'>
           <input type="button" v-if="lists.nursingStatus == 1" value='已完成' class='finished'>
       </li>
     </ul>
   </div>
  </div>
</template>

<script>
export default {
  name: 'homePage',
  data () {
    return {
      list:[],
      imgUrls:"",
      name:""
    }
  },
  mounted () {   
    var that = this;
    that.name=localStorage.name;
    that.imgUrls = that.$root.imgUrl;
    var userId = localStorage.userId;
    var timestamp = new Date().getTime(); // 时间戳
    var url = this.$root.baseUrl+"/stay/Tstay/getOldManList.html?requestType=beadhouseWeb&timestamp=" + timestamp + "&version=V1.0&userId=" + userId;
    url = this.$root.generateUrl(url)
    this.$ajax({
      method: 'get',
      url: url,
      data: {}
    })
    //成功回调
    .then(function (data) {
      // console.log(data);  
      that.list = data.data.data;
      localStorage.setItem("beadhouseId",data.data.data[0].beadhouseId)
    })
    //失败回调
    .catch(function (error) {
      console.log(error);
    });

  },
  methods:{
    details:function(id,beadhouseId,oldManName,stayType,photo,nursingStatus){
      // console.log(id)
      // console.log(beadhouseId)
      this.$router.push({path:'/nursing',query:{id:id,beadhouseId:beadhouseId,oldManName:oldManName,stayType:stayType,photo:photo,nursingStatus:nursingStatus}})
    },
    my(){
      this.$router.push({path:'/myCenter'})
    }

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  width: 100%;
  height: 13.4rem;
}
.banner{
  width: 100%;
  height: 6.3rem;
}
.title{
  height: 58px;
  width: 100%;
  text-align: center;
  line-height: 67px;
  color: #ff9000;
 
}
.title h2{
  font-size: 18px;
   font-weight:normal;
}
.title img{
  width: 19px;
  height: 19px;
  float: right;
  margin-top:-45px;
  margin-right:15px;
}
.banner img{
  height: 100%;
  width: 100%;
}
h1{
  height: 41px;
  width: 100%;
  font-weight: normal;
  text-align: center;
  font-size: 13px;  
  line-height: 41px;
  margin-top: 10px;
  margin-bottom: 10px;
}
ul{
  margin-top: 5px;
}
ul li{
  width: 33%;
  float: left;
  list-style: none;
  height: 5.09rem;
}
ul li img{
  height: 70px;
  width: 70px;
  border-radius: 50%;
}
ul li p{
  font-size: 11px;
  margin-top: 6px;
  margin-bottom:6px;
}
ul li input{
  margin-top: 3px;
  margin-bottom: 30px;
  width: 80px;
  height: 23px;
  outline: none;
  border: none;
  border-radius: 6px;
  color: #fff;
  font-size: 12px;
  outline: none;
}
.unfinished{
  background: #ff9000;
  color: #fff;
}
.finished{
  background: #fff;
  color: #ff9000;
  border:1px solid #ff9000;
}
</style>



