<template>
  <div class="changepassword">
     <div class="title">
        <img src="../assets/hg4_03.png" alt="" @click="returns"><h1>修改密码</h1>
    </div>
    <form action="">
      <div>
        <label for="">旧密码:</label> <input type="password" v-model='oldpassword'>
      </div>

      <div>
        <label for="">新密码:</label> <input type="password" v-model='newpassword'>
      </div>

      <div>
        <label for="">确认密码:</label> <input type="password" v-model='repassword'>
      </div>

      <button @click='btn'>确认</button>
    </form>

  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'changepassword',
  data () {
    return {
      oldPassword:"",
      newPassword:"",
      rePassword:"",
      userId:""
    }
  },
  mounted () {   
   

  },
  methods:{
      returns() {
        this.$router.go(-1)
      },
      btn(){
        this.oldPassword = this.oldpassword;
        this.newPassword = this.newpassword;
        this.rePassword = this.repassword;
        this.userId = localStorage.userId;
        var that = this;
        var param = {};
        param = {"oldPassword":this.oldPassword,"newPassword":this.newPassword,"rePassword":this.rePassword}
        param = Base64.encode(JSON.stringify(param))
        var timestamp = new Date().getTime(); // 时间戳
        var url = this.$root.baseUrl+"/login/modifyPassword.html?requestType=beadhouseWeb&version=V1.0&timestamp=" + timestamp + "&userId=" + this.userId + "&param=" + param;
        url = this.$root.generateUrl(url)
        this.$ajax({
          method: 'get',
          url: url,
          data: {
            param:param
          }
        })
        //成功回调
        .then(function (data) {
          console.log(data); 
          if(data.data.responseCode == 'success'){
            Toast({
              message: '修改成功',
              position: 'bottom',
              duration: 1000
            });
            that.$router.push({path:'/'}) 
          }else{
            Toast({
              message: data.data.msg.description,
              position: 'bottom',
              duration: 1000
            });
          }
         
          // localStorage.setItem("userId",data.data.data.id)
          // localStorage.setItem("name",data.data.data.name)
        })
        //失败回调
        .catch(function (error) {
          console.log(error);
        });

      }

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #eee;
}
.title img{
  position: absolute;
  left: 15px;
  width: 12px;
  height: 21px;
  top: 16px;
}
.title h1{
  font-size:13px;
  color: #ff9000;
  text-align: center;
  line-height: 70px;
  font-size: 20px;
  font-weight: normal;
}
form{
  width: 60%;
  height: 300px;
  margin:100px auto;
}
form div{
  height: 90px;
  width: 100%;
  border-bottom:1px solid #dfdfdf;
}
form div label{
  color: #999999;
  font-size: 18px;
  float: left;
  margin-top: 50px;
  margin-left:15px;
  margin-right:20px;
}
form div input{
  float: left;
  height: 50px;
  width: 70%;
  margin-top: 39px;
  font-size: 18px;
  border:none;
  outline: none;
  margin-left: -10px;
  color: #999;  
}
form button{
  width: 100%;
  height: 45px;
  background: #ff9000;
  outline: none;
  border:none;
  font-size: 17px;
  color: #fff;
  border-radius: 6px;
  margin-top: 100px;
}
</style>



