
<template>
    
    <div class="container">
      <div class="title">
        <img src="../assets/hg4_03.png" alt="" @click="returns"><h1>日常护理</h1>
      </div>
   
   
     
   
    <div class="time">

    <div style='width:100%'>
        <!-- 年的下拉 -->
      <label id="lblSelect" style='margin-right: 30px;'>
        <select id="selectPointOfInterest" v-model='year' @change="yearChange" style='background-color:#fff'>
          <option v-for="(option,index) in yearList" :value="option.value">
            {{option.label}}
          </option>
        </select>
      </label>
        <!-- 月的下拉 -->
      <label id="lblSelect" style='margin-right: 30px;'>
        <select id="selectPointOfInterest" v-model='Month' @change="MonthChange" style='background-color:#fff'>
          <option v-for="(option,index) in MonthList" :value="option.value">
            {{option.label}}
          </option>
        </select>
      </label>
        <!-- 日的下拉 -->
      <label id="lblSelect" style='margin-right: 30px;'>
        <select id="selectPointOfInterest" v-model='data' style='background-color:#fff'>
          <option v-for="(option,index) in dataList" :value="option.value">
            {{option.label}}
          </option>
        </select>
      </label>
    </div>
    
    <div style='width:100%'>
         <!-- 年的下拉 -->
      <label id="lblSelect" style='margin-right: 30px;clear:both'>
        <select id="selectPointOfInterest" v-model='years' @change="yearChanges" style='background-color:#fff'>
          <option v-for="(option,index) in yearLists" :value="option.value">
            {{option.label}}
          </option>
        </select>
      </label>
        <!-- 月的下拉 -->
      <label id="lblSelect" style='margin-right: 30px;'>
        <select id="selectPointOfInterest" v-model='Months' @change="MonthChanges" style='background-color:#fff'>
          <option v-for="(option,index) in MonthLists" :value="option.value">
            {{option.label}}
          </option>
        </select>
      </label>
      <label id="lblSelect" style='margin-right: 30px;'>
        <!-- 日的下拉 -->
        <select id="selectPointOfInterest" v-model='datas' @click='button' style='background-color:#fff'>
          <option v-for="(option,index) in dataLists" :value="option.value">
            {{option.label}}
          </option>
        </select>
      </label>
    </div>

        <!-- <input type="button" value="确定" @click='button'> -->
      </div>
    
    <div class="bigbox" style='overflow:scroll;width:100%;'>
      <table class="ListTable" cellspacing="0" cellpadding="0" >
        <thead style="border-bottom:1px solid #cccccc;">
          <tr>
            <th style='font-size:14px;' class='first'>
             <span style='color:#ff9926;position:absolute;top:50px;left:20px;font-size:19px;font-weight:normal'>护工</span>
             <span style='color:#6ad0f7;position:absolute;top:20px;right:20px;font-size:19px;font-weight:normal'>时间</span>
            </th>
            <th style='font-weight:600;' v-for="i in dateList">{{i}}</th>
          </tr>
          
        </thead>
        <tbody>
          <tr>
            <td style='font-size:19px;display:block;line-height:90px;font-size:23px;'>{{oldManName}}</td>
            <td v-for="mae in schedulingList"><span>{{mae}}</span></td>
          </tr>
        </tbody>
      </table>
  </div> 
  </div>
</template>

<script>
export default {
  name: 'scheduling',
  data () {
    var date=new Date,yearList=[];
    var year=date.getFullYear();
    var dates=new Date,yearLists=[];
    var years=dates.getFullYear();
    return {
      //labelCss变量默认为未勾选
      labelCss:"labelCss",
      // year为默认当前年
      year:year,
      Month:"",
      data:"",
      yearList:[],
      MonthList:[],
      dataList:[],

      years:years,
      Months:"",
      datas:"",
      yearLists:[],
      MonthLists:[],
      dataLists:[],

      utils:true,
      beadhouseIdss:"",
      userId:"",
      schedulingList:"",
      dateList:"",
      oldManName:"",
      schedulingDateBegin:"",
      schedulingDateEnd:"",
      y:"",
      m:"",
      d:"",
      ys:"",
      ms:"",
      ds:""
      
    }

  },
  methods:{
    returns:function(){
     this.$router.go(-1);
    },
   yearChange(){
      //当年的下拉改变时清空月和日的值以及下拉 然后渲染月的下拉
      this.Month=""
      this.MonthList=[]
      this.dataList=[]
      this.data=""
      let MonthList=[]
      for(let i=1;i<=12;i++){
        MonthList.push({value:i,label:i})
      }
      this.MonthList=MonthList
    },
    yearChanges(){
      //当年的下拉改变时清空月和日的值以及下拉 然后渲染月的下拉
      this.Months=""
      this.MonthLists=[]
      this.dataLists=[]
      this.datas=""
      let MonthLists=[]
      for(let i=1;i<=12;i++){
        MonthLists.push({value:i,label:i})
      }
      this.MonthLists=MonthLists
    },

    MonthChange(){
      //当月的下拉改变时清空日的值以及下拉 然后渲染日的下拉
      this.dataList=[]
      this.data=""
      let dataList=[]
      for(let i=1;i<=this.getDaysInMonth(this.year,this.Month);i++){
        dataList.push({value:i,label:i})
      }
      this.dataList=dataList
    },
    MonthChanges(){
      //当月的下拉改变时清空日的值以及下拉 然后渲染日的下拉
      this.dataLists=[]
      this.datas=""
      let dataLists=[]
      for(let i=1;i<=this.getDaysInMonths(this.years,this.Months);i++){
        dataLists.push({value:i,label:i})
      }
      this.dataLists=dataLists
    },

    getDaysInMonth(year, month) {
      //根据传入的年和月获取这个月的天数
     var date = new Date(year, month, 1);  
     return new Date(date.getTime() - 864e5).getDate();  
    }, 
    getDaysInMonths(years, months) {
      //根据传入的年和月获取这个月的天数
     var dates = new Date(years, months, 1);  
     return new Date(dates.getTime() - 864e5).getDate();  
    },
    button(){
      this.beadhouseIds = localStorage.beadhouseId;
      this.userId = localStorage.userId;
      this.y = this.year;
      this.m = this.Month;
      this.d = this.data;
      this.ys = this.years;
      this.ms = this.Months;
      this.ds = this.datas;
      this.schedulingDateBegin = this.y+"-"+this.m+"-"+this.d;
      this.schedulingDateEnd = this.ys+"-"+this.ms+"-"+this.ds;
      var that = this;
      var param = {'beadhouseIds':this.beadhouseIds,'schedulingDateBegin':this.schedulingDateBegin,'schedulingDateEnd':this.schedulingDateEnd}
      param = Base64.encode(JSON.stringify(param))
      var timestamp = new Date().getTime(); // 时间戳
      var url = this.$root.baseUrl+"/daily/TnursingService/getSchedulingList.html?requestType=beadhouseWeb&version=V1.0&timestamp=" + timestamp + "&userId=" + this.userId + "&param=" + param;
      url = this.$root.generateUrl(url)
      this.$ajax({
        method: 'get',
        url: url,
        data: {
          param:param
        }
      })
      //成功回调
      .then(function (data) {
        console.log(data); 
        that.schedulingList = data.data.data.schedulingList;
        that.dateList = data.data.data.dateList
      })
      //失败回调
      .catch(function (error) {
        console.log(error);
      });
    } 

  },
  mounted () {
    this.oldManName = localStorage.name
    //初始化渲染年月日的下拉
    let date=new Date,yearList=[];
    let year=date.getFullYear(); 
    for(let i=1970;i<=year;i++){
      yearList.push({value:i,label:i})
    }
    //年的下拉赋值
    this.yearList=yearList
    //年的值
    this.year=year
    //执行年改变会请求到月的下拉
    this.yearChange()
    //月的值默认为1
    this.Month=1
    //执行月改变会请求到日的下拉
    this.MonthChange()
    //日的值默认为1
    this.data=1;



    //初始化渲染年月日的下拉
    let dates=new Date,yearLists=[];
    let years=dates.getFullYear(); 
    for(let i=1970;i<=years;i++){
      yearLists.push({value:i,label:i})
    }
    //年的下拉赋值
    this.yearLists=yearLists
    //年的值
    this.years=years
    //执行年改变会请求到月的下拉
    this.yearChanges()
    //月的值默认为1
    this.Months=1
    //执行月改变会请求到日的下拉
    this.MonthChanges()
    //日的值默认为1
    this.datas=1

    this.beadhouseIds = localStorage.beadhouseId;
    this.userId = localStorage.userId
    var that = this;
    var param = {'beadhouseIds':this.beadhouseIds}
    param = Base64.encode(JSON.stringify(param))
    var timestamp = new Date().getTime(); // 时间戳
    var url = this.$root.baseUrl+"/daily/TnursingService/getSchedulingList.html?requestType=beadhouseWeb&version=V1.0&timestamp=" + timestamp + "&userId=" + this.userId + "&param=" + param;
    url = this.$root.generateUrl(url)
    this.$ajax({
      method: 'get',
      url: url,
      data: {
        param:param
      }
    })
    //成功回调
    .then(function (data) {
      console.log(data); 
      that.schedulingList = data.data.data.schedulingList;
      that.dateList = data.data.data.dateList
    })
    //失败回调
    .catch(function (error) {
      console.log(error);
    });

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container{
  width: 100%;
}
.title{
  width: 100%;
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #eee;
}
.title img{
  position: absolute;
  left: 15px;
  width: 12px;
  height: 21px;
  top: 16px;
}
.title h1{
  font-size:20px;
  color: #ff9000;
  text-align: center;
  line-height: 65px;
  font-size: 18px;
  font-weight: normal;
}
.time{
  width: 100%;
  height:100px;
  background: #f9f9f9;
  margin-top: 1px;
  line-height: 30px;
  padding-left:25px;
}
.time select{
  float: left;
  margin-top:11px;
  width: 100px;
  height: 26px;
}
.time input{
  width: 75px;
  height: 26px;
  background: #ff9000;
  color: #fff;
  border: none;
  border-radius: 3px;
  float: left;
  margin-left:15px;
  margin-top: 11px;
  outline: none;
}
.ListTable{
  width:100px;
  margin-top: 1px;
  border:1px solid #cccccc;
  overflow-x:auto 
}
.ListTable thead tr{
  width:100%;
  height: 60px;
}
.ListTable td{
  border-top: 1px solid #cccccc;
  border-left:1px solid #cccccc;
  font-size: 13px;
  width: 100px;
}
.ListTable thead tr th{
  border-left:1px solid #cccccc;
  height: 44px;
}
.ListTable tbody tr{
  width:auto;
  height: 60px;
}
.ListTable tbody tr td span{
  display: block;
  height: 5px;
  line-height: 1px;
  margin-top: 10px;
  width: 80px;
}
.ListTable thead th{
  width:110px;/*这里需要自己调整，根据自己的需求调整宽度*/
  height:50px;/*这里需要自己调整，根据自己的需求调整高度*/
  position: relative;
}
.ListTable thead th[class=first]:before{
  content: "";
  position: absolute;
  width: 1px;
  height:119px;/*这里需要自己调整，根据td的宽度和高度*/
  top:0;
  left:0;
  background-color: #d1d1d1;
  display: block;
  transform: rotate(-59deg);/*这里需要自己调整，根据线的位置*/
  transform-origin: top;
}
select#selectPointOfInterest
{
   width                    : 58px;
   height                   : 20px;
   line-height              : 20px;
   text-align               : left;
   vertical-align           : middle;
   border                   : 1px solid #f2f2f2;
   -moz-border-radius       : 6px;
   -webkit-border-radius    : 6px;
   border-radius            : 3px;
   -webkit-appearance       : none;
   -moz-appearance          : none;
   appearance               : none;
   font-family              : SimHei;
   font-size                : 14px;
   font-weight              : 500;
   color                    : RGBA(102,102,102,0.7);
   cursor                   : pointer;
   outline                  : none;
   padding-right: 3px;
}


/*LABEL FOR SELECT*/
label#lblSelect{ position: relative; display: inline-block;}


/*DOWNWARD ARROW (25bc)*/
label#lblSelect::after
{
    content                 : "\25bc";
    position                : absolute;
    top                     : 13px;
    right                   : -12px;
    bottom                  : 0;
    width                   : 15px;
    height: 18px;
    line-height             : 20px;
    vertical-align          : middle;
    text-align              : center;
    background              : #e3e3e3;
    color                   : #999999;
   -moz-border-radius       : 0 3px 3px 0;
   -webkit-border-radius    : 0 3px 3px 0;
    border-radius           : 0 3px 3px 0;
    pointer-events          : none;
}
</style>



