<template>
  <div class="container">
  <img src="../assets/btn_02.png" alt="" class='backgroundColors' style='no-repeat top left/cover;width:100%;
    height:100%;position:fixed;left:0;bottom:0;top:0'>
    <div class="bigBn">
        <form action="">
          <div class='username'>
            <img src="../assets/user_03.png" alt="">
            <label for="">用户名</label><input type="text" placeholder="" v-model="userName"><br>
          </div>
          <div class="password">
            <img src="../assets/password_03.png" alt="">
            <label for="">密&nbsp;&nbsp;&nbsp;码</label>
            <input type="password" placeholder="" v-model="passWord" v-if='ifDisplay'>
            <input type="text" placeholder="" v-model="passWord" v-else>
            <br>
            <img :src="srcs" alt="" class='yan' @click='yan' >
          </div>
          
        </form>
        <button @click='btn'>登录</button>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'login',
  data () {
    return {
      //定义
      mobile:"",
      password:"",
      userName:"",
      passWord:"",
      position:"",
      srcs:require("../assets/yan_03.png"),
      ifDisplay:true
    }
  },
  methods:{
      btn:function(){
        this.mobile = this.userName;
        this.password = this.passWord;
        this.position = 2
        var that = this;
        var param={};
        param={"mobile":this.mobile, "password":this.password,"position":this.position}
        param = Base64.encode(JSON.stringify(param))
        var timestamp = new Date().getTime(); // 时间戳
        var url = this.$root.baseUrl+"/login/login.html?requestType=beadhouseWeb&version=V1.0&timestamp=" + timestamp + "&param=" + param;
        // var url = "http://192.168.13.159:7373/beadhouse_webapi/login/login.html?requestType=beadhouseWeb&version=V1.0&timestamp=" + timestamp + "&param=" + param;
        url = this.$root.generateUrl(url)
        this.$ajax({
          method: 'get',
          url: url,
          data: {
            param:param
          }
          //,headers: {'Content-Type': 'application/x-www-form-urlencoded'}

        })
        //成功回调
        .then(function (data) {
          console.log(data); 
          if(data.data.responseCode == "success"){
            if(data.data.data.position == "2"){
              Toast({
                message: '登录成功',
                position: 'bottom',
                duration: 1000
              });
              that.$router.push({path:'/homePage/'}) 
            }else{
              Toast({
                message: '非法登录',
                position: 'bottom',
                duration: 1000
              });
            }
            
          }else{
            Toast({
              message: data.data.msg.description,
              position: 'bottom',
              duration: 1000
            });
          }
          
          localStorage.setItem("userId",data.data.data.id)
          localStorage.setItem("name",data.data.data.name)
          localStorage.setItem("type",1)

        })
        //失败回调
        .catch(function (error) {
          console.log(error);
        });
      },
      yan(){
        this.srcs = this.srcs == require('../assets/yan_03.png')?require('../assets/jing_03.png'):require('../assets/yan_03.png');
        this.ifDisplay =this.ifDisplay == true?false:true
      },
  },
  mounted () {
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
  padding: 0;
  margin:0;
}
.container{
  width: 100%;
  height: 100%;
}
.bigBn{
  width: 100%;
  height: 8rem;
  position: relative;
}
.bigBn div{
  height: 70px;
  margin-left: 10%;
  border-bottom: 1px solid #fff;
  line-height: 70px;
  margin-top: 20px;
}
.bigBn div img{
  width: 15px;
  height: 20px;
  float: left;
  margin-top:42px;
  margin-left: 16px;
}
.bigBn div .yan{
  width: 26px;
  height: 18px;
  position: absolute;
  right:30px;
  bottom: 180px;
}
form{
  width: 80%;
  height: 350px;
  background: #ff9000;
  position: absolute;
  top:50%;
  left: 10%;
  opacity: 0.4;
}
form label,input{
  margin-top: 12px;
  outline: none;
  border: none;
  background: none
}
form input{
  float: left;
  width: 60%;
  margin-left: 15px;
  margin-top: 42px;
  font-size: 14px;
}
form label{
  float: left;
  margin-left: 15px;
  font-size: 15px;
  margin-top:15px;
  color: #fff;
}
button{
  position: absolute;
  bottom: -160px;
  width: 50%;
  height: 40px;
  outline: none;
  background: #ff9000;
  border: none;
  text-align: center;
  line-height: 40px;
  color: #fff;
  border-radius: 5px;
  margin-left: -25%;
  font-size: 15px;
  margin-bottom: 20px;
}
</style>


