// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import qs from 'qs'
//md5加密
import md5 from 'js-md5'
//base64加密
let Base64 = require('js-base64').Base64;
Vue.config.productionTip = false
Vue.prototype.$ajax = axios
Vue.prototype.md5 = md5;

import Mint from 'mint-ui';
Vue.use(Mint);
console.log(Mint)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  data () {
    return {
      // baseUrl: process.env.NODE_ENV !=='production' ? '/api' : 'http://192.168.13.7:8013/beadhouse-webapi',
      // baseUrl: process.env.NODE_ENV !=='production' ? '/api' : 'http://192.168.13.168:7373/beadhouse_webapi/',
      baseUrl: process.env.NODE_ENV !=='production' ? '/api' : 'http://www.zyailao.com:8013/beadhouse-webapi',
      // imgUrl:'http://192.168.13.7:8013/beadhouse'
      imgUrl:'http://www.zyailao.com:8013/beadhouse'
    }
  },
  methods:{
  	generateUrl:function(url) {
		var params = url.split("?")[1];
		var objectList = new Array();
		for(var i in params.split("&")) {
			var param = params.split("&")[i];
			var map = new Object();
			//a.substr(a.indexOf('=') + 1, a.length)
			map.key = param.substr(0, param.indexOf('='));
			map.value = param.substr(param.indexOf('=') + 1, param.length);
			objectList.push(map);
		}
		objectList.sort(function(a, b) {
			return a.key > b.key;
		});
		var newParams = "";
		for(var i = 0; i < objectList.length; i++) {
			newParams += objectList[i].key + "=" + objectList[i].value + "&";
		}
		newParams = newParams.substring(0, newParams.length - 1);
		var sign = this.md5(newParams).toUpperCase();
		newParams = newParams + "&sign=" + sign;
		return url.split("?")[0] + "?" + newParams;
	}
  }

})



