<template>

  <div class="nursing">
      <div class="title">
        <img src="../assets/hg4_03.png" alt="" @click="returns"><h1>日常护理</h1>
      </div>
      <div class="middle">
          <img :src="imgUrls+photo" v-if='photo'>
          <img src='../assets/hg2_05.png' v-else>
          <h2>{{oldManName}}</h2>
      </div>
      <div class="details">
        <div class='top'>
          <h3>{{datas[0].name}}</h3>
          <input type="text" :placeholder="placeholderemotion" v-model="emotion">
        </div>
        <div class='bottom'>
          <h4>{{datas[1].name}}</h4>
          <input type="text" :placeholder="placeholderbehavior" v-model="behavior">
        </div>
      </div>
     
      <div class="checks">
        <div class="nursingList" id='nursingList'>
          <label :id="labelCss" @click="labelClick"></label>
          <span>{{datas[2].name}}</span>
        </div>
        <div class="nursingList">
          <label :id="labels" @click="labelClicks"></label>
          <span>{{datas[3].name}}</span>
        </div>
        <div class="nursingList">
          <label :id="labeled" @click="labelClicked"></label>
          <span>{{datas[4].name}}</span>
        </div>
          
      </div>
      
      <div class="foot" @click='btn' v-if="nursingStatuseds == '0'">
        <button>确认</button>
      </div>
      <div class="foot" @click='btn' v-if="nursingStatuseds == '1'">

      </div>

      <div class="mask" id='mask' v-show="willShow"></div>
      <div class="setting" id='setting' v-show="willShow" >
        <h1>您确定提交吗？</h1>
        <span class='cancel' @click='sure' style="color: #fff;float:left;margin-left:10%;">确定</span><span class = 'sure' @click='cancel' style='background:#cccccc;float:right;margin-right:10%'>返回</span>
      </div>

  </div>
</template>

<script>
export default {
  name: 'nursing',
  data () {
    return {
      labelCss:"labelCss",
      labels:"labelCss",
      labeled:"labelCss",
      stayId:"",
      beadhouseId:"",
      userId:'',
      stayType:"",
      oldManName:"",
      datas:[],
      willShow:false,
      emotion:"",
      nursingStatus:"",
      behavior:"",
      checked:"",
      checkedOne:false,
      checkedTwo:false,
      checkedThree:false,
      imgUrls:"",
      photo:"",
      placeholderemotion:"",
      placeholderbehavior:"",
      nursingStatuseds:""
    }
  },
  
  mounted () {
    this.imgUrls = this.$root.imgUrl;
    this.oldManName = this.$route.query.oldManName;
    this.userId = localStorage.userId;
    this.stayId = this.$route.query.id;
    this.photo = this.$route.query.photo;
    this.beadhouseId = this.$route.query.beadhouseId;
    this.stayType = this.$route.query.stayType;
    this.nursingStatuseds=this.$route.query.nursingStatus;
    var that = this;
    var param={};
    param={"stayId":this.stayId, "beadhouseId":this.beadhouseId}
    param = Base64.encode(JSON.stringify(param))
    var timestamp = new Date().getTime(); // 时间戳
    var url = this.$root.baseUrl+"/daily/TnursingService/getNursingList.html?requestType=beadhouseWeb&version=V1.0&timestamp=" + timestamp + "&userId=" + this.userId + "&param=" + param;
    url = this.$root.generateUrl(url)
    this.$ajax({
      method: 'get',
      url: url,
      data: {
        param:param
      }
    })
    //成功回调
    .then(function (data) {
      console.log(data); 
      that.datas = data.data.data;
      if(that.datas[0].nursingContent == undefined){
        that.placeholderemotion = "请输入老人的情绪"
      }else if(that.datas[0].nursingContent!= ""){
        that.emotion =that.datas[0].nursingContent
      };

      if(that.datas[1].nursingContent == undefined){
        that.placeholderbehavior = "请输入老人的行为"
      }else if(that.datas[1].nursingContent!= ""){
        that.behavior =that.datas[1].nursingContent
      };

      if(that.datas[2].nursingStatus == undefined || that.datas[2].nursingStatus == "0"){
        that.checkedOne = false
      }else if(that.datas[2].nursingStatus == "1"){
        that.checkedOne = true
        that.labelCss = "labelId";
      };

      if(that.datas[3].nursingStatus == undefined || that.datas[3].nursingStatus == "0"){
        that.checkedTwo = false;
      }else if(that.datas[3].nursingStatus == "1"){
        that.checkedTwo = true;
        that.labels = "labelId";
      };
      
      if(that.datas[4].nursingStatus == undefined || that.datas[4].nursingStatus == "0"){
        that.checkedThree = false
      }else if(that.datas[4].nursingStatus == "1"){
        that.checkedThree = true;
        that.labeled = "labelId";
      };





    })
    //失败回调
    .catch(function (error) {
      console.log(error);
    });
   
  },
  methods: {
    labelClick(){
      //判断他为勾选 为勾选就让他变成为勾选  为勾选就让他变成未勾选
      this.labelCss=this.labelCss=="labelCss"?"labelId":"labelCss";
      this.checkedOne = this.checkedOne == false?true:false
    },
    labelClicks(){
      this.labels=this.labels=="labelCss"?"labelId":"labelCss"
      this.checkedTwo = this.checkedTwo == false?true:false
    },
    labelClicked(){
      this.labeled=this.labeled=="labelCss"?"labelId":"labelCss"
      this.checkedThree = this.checkedThree == false?true:false
    },
    btn:function(){
      if(this.willShow==false){             
        this.willShow=true;           
      }else{             
        this.willShow=false           
      } 
    },
    cancel:function(){
      this.willShow=false
    },
    sure:function(){
      var that = this;
      var tnursingServiceInputList=[];
      this.emotion = this.emotion;
      this.behavior = this.behavior;
      if(this.emotion == ""){
        var params = {"id":this.datas[0].id,"beadhouseId":this.beadhouseId,"stayId":this.stayId,"stayType":this.stayType,"nursingProjectId":this.datas[0].nursingProjectId,"nursingContent":this.emotion,"nursingStatus":0};
      }else{
         var params = {"id":this.datas[0].id,"beadhouseId":this.beadhouseId,"stayId":this.stayId,"stayType":this.stayType,"nursingProjectId":this.datas[0].nursingProjectId,"nursingContent":this.emotion,"nursingStatus":1};
      }
      tnursingServiceInputList.push(params);
      if(this.behavior == ""){
        var paramOne = {"id":this.datas[1].id,"beadhouseId":this.beadhouseId,"stayId":this.stayId,"stayType":this.stayType,"nursingProjectId":this.datas[1].nursingProjectId,"nursingContent":this.behavior,"nursingStatus":0};
      }else{
        var paramOne = {"id":this.datas[1].id,"beadhouseId":this.beadhouseId,"stayId":this.stayId,"stayType":this.stayType,"nursingProjectId":this.datas[1].nursingProjectId,"nursingContent":this.behavior,"nursingStatus":1};
      } 
      tnursingServiceInputList.push(paramOne)
      if(this.checkedOne == true){
        this.nursingStatus = 1
        var paramTwo = {"id":this.datas[2].id,"beadhouseId":this.beadhouseId,"stayType":this.stayType,"stayId":this.stayId,"nursingProjectId":this.datas[2].nursingProjectId,"nursingStatus":this.nursingStatus};
      }else{
        this.nursingStatus = 0
        var paramTwo = {"id":this.datas[2].id,"beadhouseId":this.beadhouseId,"stayType":this.stayType,"stayId":this.stayId,"nursingProjectId":this.datas[2].nursingProjectId,"nursingStatus":this.nursingStatus};
      }
      tnursingServiceInputList.push(paramTwo)
      if(this.checkedTwo == true){
        this.nursingStatus = 1
        var paramThree = {"id":this.datas[3].id,"beadhouseId":this.beadhouseId,"stayType":this.stayType,"stayId":this.stayId,"nursingProjectId":this.datas[3].nursingProjectId,"nursingStatus":this.nursingStatus};
      }else{
        this.nursingStatus = 0
        var paramThree = {"id":this.datas[3].id,"beadhouseId":this.beadhouseId,"stayType":this.stayType,"stayId":this.stayId,"nursingProjectId":this.datas[3].nursingProjectId,"nursingStatus":this.nursingStatus};
      }
      tnursingServiceInputList.push(paramThree)
      if(this.checkedThree == true){
        this.nursingStatus = 1
        var paramFour = {"id":this.datas[4].id,"beadhouseId":this.beadhouseId,"stayType":this.stayType,"stayId":this.stayId,"nursingProjectId":this.datas[4].nursingProjectId,"nursingStatus":this.nursingStatus};
      }else{
        this.nursingStatus = 0
        var paramFour = {"id":this.datas[4].id,"beadhouseId":this.beadhouseId,"stayType":this.stayType,"stayId":this.stayId,"nursingProjectId":this.datas[4].nursingProjectId,"nursingStatus":this.nursingStatus};
      }
      tnursingServiceInputList.push(paramFour)
      var param = {"tnursingServiceInputList":tnursingServiceInputList}
      param = Base64.encode(JSON.stringify(param))
      var timestamp = new Date().getTime(); // 时间戳
      var url = this.$root.baseUrl+"/daily/TnursingService/addNursing.html?requestType=beadhouseWeb&version=V1.0&timestamp=" + timestamp + "&userId=" + this.userId + "&param=" + param;
      url = this.$root.generateUrl(url)
      this.$ajax({
        method: 'get',
        url: url,
        data: {
          param:param
        }
      })
      //成功回调
      .then(function (data) {
        console.log(data); 
        if(data.data.responseCode == 'success'){
          that.willShow=false;
          that.$router.go(-1);
        }else{
          Toast({
              message: data.data.msg.description,
              position: 'bottom',
              duration: 1000
            });
          }
        
      })
      //失败回调
      .catch(function (error) {
        console.log(error);
      });
      

      

    },
    returns:function(){
     this.$router.go(-1);
    }

  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/*.nursing{
  width: 1280px;
}*/
.title{
  height: 60px;
  line-height: 100px;
  border-bottom: 1px solid #eee;
}
.title img{
  position: absolute;
  left: 15px;
  width: 12px;
  height: 21px;
  top: 17px;
}
.title h1{
  font-size:18px;
  color: #ff9000;
  text-align: center;
  line-height: 70px;
  font-weight: normal;
}
.middle{
  height: 130px;
  text-align: center;
}
.middle img{
  width: 3rem;
  height: 3rem;
  margin-top: 30px;
  border-radius: 50%;
}
.middle h2{
  font-size:14px;
  margin-top: 16px;
  font-weight: normal;
}
.details{
  height: 240px;
  background: #ffefdb;
  margin-top: 80px;
}
.top{
  height: 119px;
  border-bottom: 1px solid #fff;
}
.top h3{
  float: left;
  font-size: 15px;
  text-align: center;
  line-height: 119px;
  width: 15%;
  border-right: 1px solid #ffbf6d;
  font-weight: normal;
}
.top input{
  width: 75%;
  height: 80px;
  background: #fff;
  border-radius: 4px;
  outline: none;
  border: none;
  margin-top: 20px;
  padding-left: 10px;
 font-size: 14px;
 outline: none;
}
input[type='text'] placeholder{
 color: #d0d0d0;
}
.bottom h4{
  float: left;
  font-size: 15px;
  text-align: center;
  line-height: 119px;
  width: 15%;
  border-right: 1px solid #ffbf6d;
  font-weight: normal;
}
.bottom input{
  font-size: 14px;
  width: 75%;
  height: 80px;
  background: #fff;
  border-radius: 4px;
  outline: none;
  border: none;
  margin-top: 20px;
  padding-left: 10px;
  outline: none;
}
.checks div{
  float: left;
  width: 26%;
  margin-top: 35px;
}
.foot{
  margin-top: 28%;
  width: 100%;
  text-align: center;
}
.foot button{
  height: 43px;
  width: 80%;
  background: #ff9000;
  border-radius: 5px;
  border: none;
  color: #fff;
  font-size: 15px;
  outline: none;
}
.mask{
  min-width: 320px;
  min-height: 100%;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: .5;
  z-index: 999;
  position: absolute;
  top: 0;
  left: 0;
}
.setting{
  width: 66%;
  height: 140px;
  border-radius: 15px;
  margin-left: 17%;
  background: #FFFFFF;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  z-index: 999;
}
.setting h1{
  margin-top: 15px;
  font-size: 15px;
  font-weight: normal;
  text-align: center;
  margin-bottom: 15px;
  line-height: 40px;
  color: #414141;
}
.setting span{
  font-size: 14px;
  display: block;
  width: 30%;
  text-align: center;
  float: left;
  background: #ff9000;
  height: 32px;
  margin-top: 15px;
  line-height: 32px;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
}
.setting .cancel{
  color: #fff;     
}
.nursingList{
  height: 15px;
  position: relative;
}
.nursingList span{
  display: block;
  float: left;
  line-height: 20px;
  font-size: 12px;
  padding-left: 10px;

}
.nursingList #labelId{ 
  width:20px;
  display: block; 
  height:20px; 
  border-radius:3px;
  float: left; 
  background-color: #ff9000; 
} 
.nursingList #labelId:after { 
  content: '\00a0'; display: inline-block; 
  border: 2px solid #fff; 
  border-top-width: 0; 
  border-right-width: 0; 
  width: 13px; 
  height: 8px; 
  -webkit-transform: rotate(-50deg); 
  position: absolute; 
  top:2px; 
  left:2px; 
}
 .nursingList #labelCss{ 
  width:20px;
  display: block; 
  height:20px; 
  border-radius:2px; 
  float: left;
  background-color: #ff9000; 
  position: relative; 
}
#nursingList{
  margin-left:17%;
}

</style>


